.BlogItem {
  border-radius: 10px;
  background-color: var(--deep-blue);
  margin-top: 2%;
  overflow: hidden;
  margin: 1%;
  .BlogItem-img {
    padding-bottom: 100%;
    position: relative;
    overflow: hidden;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .BlogItem-img-mask {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 50%;
      background: linear-gradient(0deg, #1C335F 0%, rgba(255, 255, 255, 0) 100%);
    }
  }
  .BlogItem-content {
    position: relative;
    margin-top: -20%;
    padding: 0 5%;
    color: white;
    .BlogItem-content-tags {
      color: var(--main-color);
      font-weight: bolder;
      font-size: 1.2vw;
    }
    .BlogItem-content-title {
      font-size: 1.8vw;
    }
    .BlogItem-content-shortMain {
      font-size: 1.3vw;
      opacity: 0.6;
      margin: 1vw 0 2vw 0;
    }
  }
  .BlogItem-publisher {
    display: flex;
    font-size: 1.6vw;
    display: flex;
    align-items: center;
    padding: 5%;
    .BlogItem-publisher-avatar{
      width: 3vw;
      // height: 3vw;
      // background-color: var(--main-color);
      // border-radius: 1vw;
      // overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
    }
    .BlogItem-publisher-name {
      color: var(--main-color);
      margin: 0 4%;
      flex: 1;
      white-space: nowrap;
    }
    .BlogItem-publisher-date {
      color: white;
      opacity: 0.6;
    }
  }
}

@media (max-width: 640px) {
  .BlogItem {
    width: 98%;
  }
}
@media (min-width: 640px) and (max-width: 768px) {
  .BlogItem {
    width: 48%;
  }
}

@media (min-width: 768px) {
  .BlogItem {
    width: 31.33%;
  }
}
@primary-color: #FF2A42;