.Tags {
  margin-top: 1vw;
  background-color: var(--deep-blue);
  border-radius: 1vw;
  padding: 1vw;
  color: white;
  .Tags-title {
    span {
      font-size: 1.8vw;
      display: block;
    }
    .Tags-title-dividing {
      width: 4vw;
      height: 0.4vw;
      background: linear-gradient(130deg, rgb(36, 198, 220), rgb(84, 51, 255) 41.07%, rgb(255, 0, 153) 76.05%);
      border-radius: 1rem;
      margin-top: 0.5vw;
    }
  }
  .Tags-title-content {
    margin-top: 1vw;
  }
}
@primary-color: #FF2A42;