.blog {
  margin: -2% 6%;
  margin-bottom: 12%;
  z-index: 9;
  position: relative;
  // height: 200vh;
  // position: relative;
  border-radius: 0 0 10px 10px;
  padding-bottom: 5%;
  background-color: #F0F6FE;
  .blog-container {
    padding-bottom: 50%;
    position: relative;
  }
  .blog-header {
    padding: 5%;
    display: flex;
    justify-content: space-between;
    .blog-header-left,.blog-header-right {
      width: 49%;
      height: 38vw;
      border-radius: 10px;
      overflow: hidden;
    }
    .blog-header-left {
      background-color: white;
      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }
    .blog-header-right {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .blog-header-right-top, .blog-header-right-bottom {
        height: 48%;
        border-radius: 10px;
        overflow: hidden;
      }
      .blog-header-right-top {
        position: relative;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
        .blog-header-right-top-mask {
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
          background: linear-gradient(0deg, #C9D5E3 0%, rgba(255, 255, 255, 0) 100%);
        }
        .blog-header-right-top-music {
          position: absolute;
          height: 40%;
          width: 100%;
          bottom: 0;
          left: 0;
          display: flex;
          padding: 2% 5%;
          justify-content: space-between;
          .music-info {
            color: white;
            .music-info-title {
              font-size: 1.8vw;
              font-weight: bold;
            }
            .music-info-name {
              font-size: 1.4vw;
              opacity: 0.9;
            }
          }
          .music-handle {
            color: white;
            display: flex;
            width: 40%;
            align-items: center;
            // 播放暂停按钮
            .music-center {
              padding: 8%;
              border-radius: 50%;
              background-color: white;
              display: flex;
              justify-content: center;
              align-items: center;
              box-shadow: var(--box-shadow);
              margin: 0 20%;
              cursor: pointer;
              transition: all .3s;
              &:hover {
                background-color: var(--main-color);
                .iconfont {
                  color: white;
                  text-shadow: var(--box-shadow);
                  transform: translate(10%,2%);
                  transition: all .3s;
                }
              }
              .iconfont {
                color: black;
                transform: translate(10%,0);
                transition: all .3s;
                font-size: 2vw;
              }
            }
            .icon-ai10, .icon-ai09{
              cursor: pointer;
              font-size: 2.5vw;
              text-shadow: var(--box-shadow);
              transition: all .3s;
              &:hover {
                color: black;
              }
            }
          }
        }
      }
      .blog-header-right-bottom {
        display: flex;
        justify-content: space-between;
        .blog-header-right-bottom-weather,.blog-header-right-bottom-date {
          width: 48%;
          border-radius: 10px;
        }
        // 天气 单独样式
        .blog-header-right-bottom-weather {
          background: linear-gradient(-30deg, #3F81D8 0%, #94B8E7 100%);
          .blog-header-right-bottom-weather-icon {
            width: 45%;
            height: 45%;
            // background-color:red;
            margin: 10% auto 5%;
            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }
          .blog-header-right-bottom-weather-data {
            color: white;
            display: flex;
            padding: 0 10%;
            .blog-header-right-bottom-weather-data-position {
              font-size: 1.7vw;
              .position-subTitle {
                font-size: 1.4vw;
                opacity: 0.9;
              }
            }
            .blog-header-right-bottom-weather-data-temperature {
              font-size: 3vw;
              display: flex;
              align-items: center;
              margin-left: 10%;
            }
          }
        }
        // 日历 单独样式
        .blog-header-right-bottom-date {
          background-color: #96BE9E;
          color: white;
          padding: 5% 5%;
          .blog-date-time {
            font-size: 4vw;
            text-align:center;
          }
          .blog-date-day {
            margin-top: 32%;
            padding-left: 30%;
            .blog-date-day-week {
              font-size: 1.7vw;
            }
            .blog-date-day-detail {
              font-size: 1.4vw;
              opacity: 0.9;
            }
          }
        }
      }
    }
  }
  .blog-main {
    // position: absolute;
    width: 100%;
    // top: 98%;
    padding: 0 3%;
    // background-color: white;
    .blog-main-content {
      display: flex;
    }
    .blog-main-title {
      font-size: 0.25rem;
      display: flex;
      align-items: center;
      color: var(--deep-blue);
      text-shadow: var(--box-shadow);
      .iconfont {
        font-size: inherit;
      }
      span {
        margin: 0 0.15rem 0 0.1rem;
        font-weight: bolder;
      }
      .blog-main-title-dividing {
        flex: 1;
        height: 0.08rem;
        border-radius: 1rem;
        background-color: var(--deep-blue);
         box-shadow: var(--box-shadow);
      }
    }
    .blog-main-list {
      display: flex;
      flex-wrap: wrap;
    }
    .blog-main-user {
      width: 25%;
      flex-shrink: 0;
      margin: 1% 0;
      // height: 20vw;
      border-radius: 1vw;
      .blog-main-user-container {
        position: sticky;
        top: 1vw;
      }
    }
    @media (max-width: 1024px) {
      .blog-main-user {
        display: none;
      }
    }
  }
}
@primary-color: #FF2A42;