.UserCard {
  padding: 2.5vw 0.5vw 0.5vw 0.5vw;
  background: linear-gradient(130deg, rgb(36, 198, 220), rgb(84, 51, 255) 41.07%, rgb(255, 0, 153) 76.05%);
  border-radius: 1vw;
  box-shadow: var(--box-shadow);
  .UserCard-content {
    background-color: var(--deep-blue);
    border-radius: 0.8vw;
    position: relative;
    padding-top: 30%;
    color: white;
    img {
      width: 35%;
      border-radius: 50%;
      position: absolute;
      top: -1.5vw;
      left: 50%;
      transform: translate(-50%);
    }
    .UserCard-content-name {
      font-size: 2vw;
      text-align: center;
      font-weight: bolder;
      .UserCard-content-name-baseLine {
        width: 20%;
        margin: 0.3vw auto 1.5vw auto;
        height: 0.3vw;
        border-radius: 1vw;
        background: linear-gradient(
          130deg
          , rgb(36, 198, 220), rgb(84, 51, 255) 41.07%, rgb(255, 0, 153) 76.05%);
      }
    }
    .UserCard-content-text {
      opacity: 0.6;
      text-align: center;
    }
    .UserCard-content-connect {
      display: flex;
      margin: 1.8vw 0vw;
      justify-content: space-evenly;
      i {
        font-size: 2.5vw;
        padding: 1vw;
        opacity: 0.8;
        transition: all .3s;
        cursor: pointer;
        &:hover {
          transform: scale(1.2);
          opacity: 1;
          text-shadow: var(--box-shadow);
          position: relative;
          &:after {
            position: absolute;
            content: attr(data-after);
            font-size: 1vw;
            padding: 0.2vw;
            white-space: nowrap;
            box-shadow: var(--box-shadow);
            color: black;
            background-color: white;
            left:50%;
            top: 0;
            border-radius: 0.5vw;
            transform: translate(-50%, -70%);
            animation: icon-move 0.3s;
            animation-iteration-count:1;
            animation-fill-mode:forwards;
          }
        }
        &.icon-weixin {
          &:hover {
            color: rgb(7,193,96);
          }
        }
        &.icon-qq {
          &:hover {
            color: rgb(104,165,225);
          }
        }
        &.icon-dianhua {
          &:hover {
            color: green;
          }
        }
        &.icon-github {
          &:hover {
            color: white;
          }
        }
      }
    }
    .UserCard-content-data {
      display: flex;
      padding-bottom: 2vw;
      justify-content: space-evenly;
      .UserCard-content-data-item {
        display: flex;
        flex-direction: column;
        text-align:center;
        font-size: 1.4vw;
        .text-opacity {
          opacity: 0.7;
          font-size: 1.2vw;
        }
      }
    }
  }
}

@keyframes icon-move {
  0% {
    transform: translate(-50%, 0);
    opacity: 0;
  }
  100% {
    transform: translate(-50%, -70%);
    opacity: 1;
  }
}
@primary-color: #FF2A42;